.navigation {
    width: 100%;
    height: 100%;
    word-wrap: break-word;
    text-overflow: ellipsis;
    overflow: auto;
}
.infoline{
  padding: 10px 15px 0 15px;
  color:#ccc;
  font-size: 15px;
  line-height: 17px;
  display: -webkit-flex;
  display: flex;
}
.info{
  flex: 1;
  display: flex;
  max-width: calc(100% - 60px);
  font-size: 15px;
}
.title{
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  display: inline-block;
}
.withCurrent{
  max-width: calc(100% - 85px);
}
.preview{
  color: #888;
  cursor: pointer;
}
.current{
  flex: 0 0 85px;
  display: inline-block;
  margin-left: 2px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.date{
  float:right;
  flex: 0 0 60px;
}

.tooltip{
  padding: 4px 8px!important;
  font-size: 12px!important;
  text-align: left!important;
  font-style: normal;
  max-width: 300px;
  word-wrap: normal;
  word-break: break-all;
}
.tooltip :global(.multi-line){
  text-align: left!important;
}



