.salesforce{
  width: 100%;
  height: 100%;
  word-wrap: break-word;
  text-overflow: ellipsis;
  overflow: auto;
  margin:0 auto;
}
.Loading {
  margin: 15px;
  color: #666;
  line-height: 1;
}
.infoline{
  padding-bottom: 13px;
  color: #666;
  line-height: 18px;
}
.infoline2{
  padding-bottom: 3px;
  color: #85bf71;
  line-height: 18px;
  text-align: left;
}

.button{
  width: 215px;
  margin: 5px;
}
.accountinfo{
  width: 100%;
  padding: 15px;
  text-align: left;
}
.accountTitle{
  padding-bottom: 10px;
  color: #888;
  line-height: 1;
}
.accountName{
  color: #666;
  display: inline-block;
  width: 40%;
  float: left;
}
.accountValue{
  color: #000;
  display: inline-block;
  width: 50%;
  line-height: 1;
}
.field {
  margin-top: 5px;
  margin-bottom: 15px;
}

.fieldName {
  font-size: 15px;
  color: #666;
  line-height: 1;
  padding-bottom: 2px;
}

.required {
  color: #d34946;
  vertical-align: middle;
}
span.required {
  padding-left: 5px;
}

.field .radio label{
  margin-bottom: 1px;
}
.inputWidth{
  width: 100%;
}
input.input {
  font-size: 15px;
  line-height: 16px;
  color: #000;
  padding: 6px 0 5px 0;
  border: 0;
  border-bottom: 1px solid #ccc;
}
.buttonWrapper {
  text-align: center;
  position: absolute;
  bottom: 10px;
  width: 100%;
}
.errorMessage {
    word-wrap: break-word;
    width: 100%;
    color: #fff;
    margin-top: 20px;
    margin-bottom: 5px;
    padding: 0 15px;
}
.errorMessage span {
    display: inline-block;
    background: #EB4F43;
    padding: 5px 8px;
    border-radius: 2px;
}
.marginRight20 {
  margin-right: 20px;
}

.successMessage {
  word-wrap: break-word;
  word-break: break-all;
  width: 100%;
  color: #74b75d;
  font-size: 12px;
  padding: 0 15px;
  margin-top: 5px;
  margin-bottom: 5px;
}
.successMessage span i {
  padding-right: 5px;
}
.success{
  width: 28px;
  height: 28px;
  float: left;
}
.Container {
  width: 100%;
  font-size: 15px;
  line-height: 18px;
  padding: 15px;
  text-align: left;
  clear: both;
}
.Container input{
  color: #000;
  font-size: 14px;
  height: 26px;
}
.Container .editIconWrapper{
  position: relative;
  float: right;
}
div.Container.editable.notEdit:hover {
  background: #e6e6e6;
}
.editIcon{
  display: none;
  cursor: pointer;
}
div.Container.editable.notEdit:hover .editIcon{
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
}
.Container a{
    text-decoration: none;
    color: #32afd9;
    cursor: pointer;
}
.leadInfo{
  color: #666;
  display: inline-block;
  width: 40%;
  float: left;
}
.leadValue{
  color: #000;
  display: inline-block;
  width: 50%;
  line-height: 1;
}
.textArea {
  color: #000;
  height: 70px;
  padding: 5px;
}

.buttonGroup{
  margin: 15px 15px 10px 15px;
}
.formTitle {
  display: inline-block;
  padding: 15px 15px 0;
  font-size: 14px;
  line-height: 1;
  color: #888;
}
.relativeDiv {
  display: flex;
  flex-direction: column;
}
.loadingDiv {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  background: #f5f5f5;
  opacity: 0.6;
  z-index: 999;
}
.formWrapper {
  position: absolute;
  top: 30px;
  bottom: 60px;
  width: 100%;
  overflow: auto;
  font-size: 15px;
  padding: 4px 15px;
}
.fieldInfo {
  padding-bottom: 8px;
  line-height: 15px;
}
.fieldName .sfcheckbox {
  top: -5px;
}


