.halfWidth {
  display: inline-block;
  padding-top: 20px;
  width: 50%;
}
.list {
  float: left;
  width: 80%;
}
.col {
  margin-right: 20px;
}
.header {
  border: 1px solid #ccc;
  padding: 5px 20px 5px 15px;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
}
.optionsWrapper {
  border: 1px solid #ccc;
  border-top: none;
  overflow: auto;
  height: 328px;
}
.option {
  color: #333;
  padding-left: 15px;
  line-height: 25px;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
}
.option:hover {
  background: #e6e6e6;
  cursor: default;
}
.selected {
  background: #cce7f5;
}
.operations {
  float: left;
  width: 20px;
  padding-top: 220px;
}
.icons {
  display: block;
  width: 16px;
  height: 16px;
}
.operation {
  margin: 20px 0;
  color: #bbbbbb;
}
.operations .operation:last-child {
  margin-bottom: 0;
}
.operation:hover {
  color: #828282;
  cursor: pointer;
}


