/* Stylesheet for Page*/
.page {
  width: 100%;
  height: 100%;
}
.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-repeat: repeat;
  background-image: url('../Image/background.jpg');
}
.container input:disabled{
  background-color: #fff;
  color: #888;
}

.login button {
  font-size: 17px;
  line-height: 20px;
}
.content {
  width: 100%;
}
.header {
  width:100%;
  height:50px;
  line-height: 50px;
  background: #f1f1f1;
  border-bottom: 1px solid #ddd;
}
.header a {
  text-decoration: none;
  color: #666;
  font-size: 14px;
  padding-left: 10px;
}
.help {
  color:#666;
  line-height: 30px;
  display: inline-block;
  vertical-align: middle;
  margin: 10px 0;
}
.help:hover {
  color: #329fd9;
}
.help i {
  margin-right: 5px;
  float: left;
  line-height: 30px;
}
.help span {
  float: left;
  line-height: 30px;
}

.loginIcon {
  display:block;
  width: 24px;
  height: 24px;
}
.footer {
  width:100%;
  color: #888;
  font-size: 13px;
  text-align: center;
  padding: 0 0 20px 0;
}

.logoContainer{
  margin: 10px 10px 12px 10px;
}
.inputWidth input{
  width:210px;
}
.errorMessageWrapper {
  text-align: center;
}
.errorMessage {
  background-color: #dc5f5f;
  text-align: left;
  margin-bottom: 20px;
  max-width: 320px;
  border-radius: 2px;
  padding: 10px 15px;
  color: #fff;
  display: inline-block;
  line-height: 16px;
}
.errorMessage a {
  display: inline-block;
  color: #fff;
  text-decoration: underline !important;
}
.errorMessage a:hover {
  cursor: pointer;
  text-decoration: underline;
}
.errorMessage .fixLink {
  color: #fff;
  margin-left: 15px;
  text-decoration: underline;
}
.errorMessage .billLink {
  padding: 0 5px;
  color: #fff;
  text-decoration: underline;
}
.errorMessage .billLink:link {
  text-decoration: underline;
}
.login {
  width: 320px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding-top: 30px;
  background-color: #fff;
}

.line {
  margin-bottom: 22px;
  text-align: center;
}
.buttonWrapper {
  text-align: center;
  margin-bottom: 15px;
}
.optionWrapper {
  margin-bottom: 20px;
  text-align: center;
}

.buttonWidth {
  width: 240px;
}

.left {
  float: left;
}
.marginLeft40{
  margin-left: 40px;
}
 .right {
  float: right;
}
.marginRight20 {
  margin-right: 20px;
}
.marginRight40 {
  margin-right: 40px;
}
.forgot {
  width: 100%;
  height: 40px;
  background-color: #f0f0f0;
  line-height: 40px;
  text-align: right;
}
.forgot a{
  font-style: normal;
  text-decoration: none;
  margin-right: 40px;
  color: #888;
  font-size: 14px;
  text-shadow: #fff 0 1px 1px;
}
.forgot a:hover{
  text-decoration:underline;
  cursor: pointer;
}
.loadingIcon {
  display:inline-block;
  width: 19px;
  height: 19px;
	margin: 0 10px;
  position: relative;
  top: 4px;
}

.homePageWraper{
  position: fixed;
  width:100%;
  height: 100%;
  overflow: hidden;
}
.checkbox {
  top: 2px;
}
.checkbox i{
  margin-top: -8px !important;
}
.checkbox span {
  font-size: 15px;
}
.waiting {
  background: #329FD9 url('../Icons/images/waiting-blue.gif') no-repeat 180px center!important;
  cursor: default;
}
.verificationCode input{
  width: 120px;
}
.verificationCode img{
  vertical-align: middle;
  width: 70px;
  padding-left: 2px;
  cursor: pointer;
}
.verificationCode i{
  vertical-align: middle;
}
.dropdownlist select+span {
  left:0;
}
.refreshIcon {
  padding-left: 5px;
  cursor: pointer;
}
.globalErrorMessageWrapper {
  text-align: center;
  width: 100%;
  position: fixed;
  top: 50px;
  z-index: 99999;
}
.globalErrorMessage {
    background-color: #ffe5dd;
    border-radius: 2px;
    padding: 5px;
    color: #eb4f43;
    font-size: 12px;
    display: inline-block;
}
.dismiss {
  color: #329fd9;
  font-size: 12px;
  display: inline-block;
  text-decoration: underline;
  margin-left: 10px;
  cursor: pointer;
}
.closeImage {
  vertical-align: top;
  cursor: pointer;
  width: 15px;
  height: 15px;
  margin-right: 10px;
  display: inline-block;
  background-repeat: no-repeat;
  background-position-y: center;
  background-size:contain;
  background-image: url('./bg_pop_close.png');
}


