/* Stylesheet for Input */

.input {
  border: 1px solid #ccc;
  padding: 5px 10px;
  border-radius: 2px;
  transition: border-color ease-in-out 0.2s;
  background-color: #fff;
  font-size: 15px;
  font-family: "SourceSans Pro";
}
.input:disabled{
  background-color: #f5f5f5;
}

.input[readOnly]{
  border:0;
}

input.attachTicket{
  width: 100px;
  margin-left: 5px;
}

.input:focus {
  border-color: #329FD9;
  outline: none;
}

.input.error, .input.error:hover, .input.error:focus {
  border-color: #d34747;
}

.prependContainer {
  display: inline-block;
}

.prepend {
  display: inline-block;
  line-height: 18px;
  padding: 5px 5px 4px 5px;
  background-color: #fff;
}

.prependContainer .input {
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
}

.prependIcon {
    float: left;
    padding: 0 6px 0 0;
}

.defaultIcon {
  display:block;
  width: 24px;
  height: 24px;
}



