.openPopup{
    position: absolute;
    z-index: 9999;
    color: #666;
}
.openPopup:after,
.openPopup:before {
    content: '';
    display: block;
    position: absolute;
    top: -20px;
    width: 0;
    height: 0;
    border-style: solid;
}
.openPopup:after {
    top: -2px;
    left: 50%;
    transform: translateX(-50%);
    border-color: transparent transparent #ddd transparent;
    border-width: 8px;
    z-index: 999;
}
.openPopup:before {
    top: 2px;
    left: 50%;
    transform: translateX(-50%);
    border-color: transparent transparent #fff transparent;
    border-width: 6px;
    z-index: 1000;
}


