.wrapup {
    /*display: flex;
    width: 100%;*/
    /*height: 100%;*/
    word-wrap: break-word;
    text-overflow: ellipsis;
    /*overflow: auto;*/
    /*flex-direction: column;
    justify-content: space-between;*/
}
.wrapupFormWrapper {
    margin-top: 10px;
}
.Container {
    font-size: 15px;
    width: 100%;
    padding: 0 15px 13px;
}
.Container input{
    font-size: 14px;
    color: #666;
}
.buttonWrapper {
  padding-top: 5px;
  text-align: center;
}
.errorMessage {
    word-wrap: break-word;
    width: 100%;
    color: #EB4F43;
    font-size: 12px;
    padding: 0 15px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.marginRight20 {
  margin-right: 20px;
}

.successMessage {
    word-wrap: break-word;
    width: 100%;
    color: #74b75d;
    font-size: 14px;
    padding: 0 15px;
    margin-top: 5px;
    margin-bottom: 5px;
}
.successMessage span i {
    padding-right: 5px;
}

.WrapupHistoryContainer {
    padding: 15px 0 20px;
}
.WrapupHistoryHeader {
    padding: 0 15px 5px;
    font-size: 14px;
}
.WrapupHistoryHeader h4{
  display: inline-block;
  margin: 0;
  color: #000;
  font-family: "SourceSans Pro Bold";
  font-weight: normal;
}
.WrapupDetailsHeader h4{
  color: #000;
  display: inline-block;
  font-family: "SourceSans Pro";
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  margin: 0 0 10px 0;
}
.WrapupHistoryHeader a, .WrapupDetailsHeader a  {
    cursor: pointer;
    float: right;
}
.Loading {
    padding: 1px 15px;
    color:#666666;
    font-size:14px;
}
.List{
    color: #666;
    font-size: 15px;
    line-height: 18px;
}
.List a{
    color: #666;
    text-decoration: none;
    padding: 2px 15px;
    display: inline-block;
    word-break: break-word;
    cursor: pointer;
}
.List a:hover{
    text-decoration: underline;
}

.WrapupDetail{
 border: 1px solid #ccc;
 background: #e6e6e6;
 margin-top: 8px;
 padding: 5px 15px 10px;
 width: 100%;
 /*box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.15);*/
}
.WrapupDetail:after,
.WrapupDetail:before {
    content: '';
    position: absolute;
    border-style: solid;
    left: 25px;
}
.WrapupDetail:after {
    top: -16px;
    transform: translateX(-50%);
    border-color: transparent transparent #ccc  transparent;
    border-width: 8px;
    z-index: 998;
    top: -15px;
    box-shadow: 0 5px #e6e6e6;
}
.WrapupDetail:before {
    top: -12px;
    transform: translateX(-50%);
    border-color: transparent transparent #e6e6e6 transparent;
    border-width: 6px;
    z-index: 999;
    top: -11px;
}

a.detailsLink {
    color: #329fd9;
    text-decoration: none;
    padding: 0;
    font-size: 13px;
}
.WrapupDetailsHeader {
    display: flex;
    justify-content: space-between;
    padding: 0;
}
.detailsContent {
    padding: 5px 0;
}
.nowrapupForm {
    padding: 15px;
    font-size: 13px;
    word-break: break-word;
}
.featrue {
    padding: 15px 15px 0 15px;
}
.featrue + .wrapupFormWrapper {
    margin-top: 0;
}
.required {
    word-wrap: break-word;
    width: 100%;
    color: #fff;
    padding: 0 15px;
    margin-top: 5px;
    margin-bottom: 5px;
}
.required span{
    background: #EB4F43;
    display: inline-block;
    padding: 5px 8px;
    border-radius: 2px;
}
.wrapupWrapper {
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    position: absolute;
}
.relative {
    position: relative;
    margin-bottom: 3px;
}
.historyItem {
  width: 100%;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space:nowrap;
}
.WrapupDetailContent {
    user-select: text;
}
.tooltip{
  padding: 4px 8px!important;
  font-size: 12px!important;
  text-align: left!important;
  font-style: normal;
  max-width: 300px;
  word-wrap: normal;
  word-break: break-all;
}
.tooltip :global(.multi-line){
  text-align: left!important;
}
.wrapupDetailsError {
    font-size: 14px;
    color: red;
    display: inline-block;
    padding-left: 5px;
}
.refreshIcon{
    cursor: pointer;
}


