.title{
  font-size: 18px;
  text-align: center;
  margin-bottom: 10px;
}
.noteinfo{
  font-size: 14px;
  color: #888;
  margin: 20px 10px;
  line-height: 1;
}
.noresult{
  font-size: 14px;
  color: #888;
  margin: 20px 10px;
  line-height: 1;
}
.listtitle{
 font-size: 14px;
 color: #000;
 margin: 15px 0 10px 10px;
 line-height: 1;
}
.infoList{
  color: #333;
  margin: 10px;
  max-height: 215px;
}
.infoContact{
  background-color: #ccc;
}
.lookupWin{
 width: 735px;
 height: 452px;
}
.lookupTool{
  text-align: center;
}
.lookupToolspan{
  margin:10px;
  position:relative;
}
.relativePosition{
  position:relative;
}
.searchIcon{
  position: absolute;
  right: 0px;
  z-index: 5;
  background-repeat: no-repeat;
  background-position: 0px 0px;
  width: 28px;
  height: 28px;
  top: 1px;
  cursor: pointer;
}
.searchInput{
  position: relative;
  width: 320px;
}
.lookupType{
  width: 100px;
  height: 30px;
}
.lookupType span{
  height: 28px;
}

.cancelButton{
  text-align: center;
  position: absolute;
  width: 90%;
  bottom: 30px;
}
.input {
  border: 1px solid #ccc;
  padding: 6px 0 5px;
  border-radius: 2px;
  transition: border-color ease-in-out 0.2s;
  background-color: #fff;
  font-size: 15px;
  line-height: 16px;
}

.input:focus {
  border-color: #329FD9;
  outline: none;
}

.input.error, .input.error:hover, .input.error:focus {
  border-color: #d34946;
}
span:focus {
  outline: none;
}
span:focus .input{
  border-color: #329FD9;
  outline: none;
}

.inputStyle{
  border: 0;
  border-bottom: 1px solid #ccc;
  width: 100%;
}

.lookupnoInfo{
 display: inline-block;
 height: 64px;
 background-color: #bcbcbc;
 color:#ccc;
 padding: 10px 20px;
}

.lookupError{
 display: inline-block;
 color: #d34946;
 margin: 20px 10px;
}

.Loading{
 width: 100%;
 text-align: center;
 height: 64px;
 padding-top: 20px;
}


.resultList{
  width: 100%;
  text-align: left;
  font-size: 15px;
  border-radius: 0 0 2px 2px;
  overflow-y: auto;
  overflow-x: hidden;
  width:100%;
  max-height: 176px;
}
.head{
  background: #e1e1e1;
  color:#333;
  height: 40px;
  display: -webkit-flex; /* Safari */
  display: flex;
  /*flex-wrap: nowrap;*/
  align-items: center;
  /*justify-content: center;*/
  border-radius: 2px 2px 0 0;
}
.head span{
  display: block;
  padding-left: 20px;
  flex-grow: 1;
  flex-basis: 1%;
  text-overflow: ellipsis;
  overflow: hidden;
  /*width:50px;*/
  white-space: nowrap;
}
.line{
  height:35px;
  display: -webkit-flex; /* Safari */
  display: flex;
  /*flex-wrap: nowrap;*/
  align-items: center;
  /*justify-content: center;*/
}
.line:nth-child(2n+1){
  background: #ebebeb;
}
.line:nth-child(2n){
  background: #f5f5f5;
}
.resultList span{
  display: block;
  padding-left: 20px;
  flex-grow: 1;
  flex-basis: 1%;
  text-overflow: ellipsis;
  overflow: hidden;
  /*width:50px;*/
  white-space: nowrap;
}
 .resultList a{
  cursor: pointer;
  color: #329fd9;
  text-overflow: ellipsis;
  overflow: hidden;
}




