.vm-tree {
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
  user-select: none;
}
.vm-tree li {
  padding: 0;
  margin: 0;
  list-style: none;
  white-space: nowrap;
  outline: 0;
}

.vm-tree li ul {
  margin: 0;
}

.vm-tree li .vm-tree-node-content-wrapper {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  display: inline-block;
  height: 28px;
  width: 100%;
  padding-right: 15px;
}
.vm-tree li span.vm-tree-switcher,
.vm-tree li span.vm-tree-icon {
  display: inline-block;
  border: 0 none;
  cursor: pointer;
  outline: none;
  vertical-align: top;
}
.vm-tree li span.vm-tree-icon{
  position: relative;
  top:3px;
}
.vm-tree-icon {
  width: 16px;
  height:16px;
}
.vm-tree li span.vm-tree-switcher i {
  margin-right: 2px;
  position: relative;
  top: 3px;
}
.vm-tree li span.vm-tree-switcher.vm-tree-switcher-noop {
  cursor: auto;
}

.vm-tree-child-tree {
  display: none;
}
.vm-tree-child-tree-open {
  display: block;
}
.vm-tree-treenode-disabled > span,
.vm-tree-treenode-disabled > a,
.vm-tree-treenode-disabled > a span {
  color: #ccc;
  cursor: not-allowed;
}
.vm-tree-node-selected {
  background-color: #ffe6b0;
  border: 1px #ffb951 solid;
  opacity: 0.8;
}
.depSpread {
  transform: rotate(-90deg);
}
.vm-tree-itemWrapper{
  /*padding: 1px 0 2px;*/
  height: 28px;
  line-height: 28px;
  white-space: nowrap;
}
div.vm-tree-itemWrapper:hover {
  background: #e6e6e6;
}
div.vm-tree-itemWrapper:focus {
  background: #cce7f5;
  border: 0;
  outline: none;
}
.vm-tree-title-wrapper {
  width: 100%;
  display: inline-block;
  position: relative;
  /*top: 8px;*/
  padding-left: 6px;
  overflow: hidden;
  text-overflow: ellipsis;
}
span.vm-tree-icon + .vm-tree-title-wrapper {
  padding-left: 7px;
}



