.customFilter {
  width: 590px;
  height: 380px;
  font-size: 15px;
}
.containerWrapper {
  height: 200px;
  overflow: auto;
}
.container {
  width: 100%;
  outline: none;
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: left;
  position: relative;
}
a {
  cursor: pointer;
}
.action{
  display: inline-block;
  position: absolute;
  bottom: 30px;
  left: 205px;
  margin-top: 10px;
}
.action button{
  margin-right: 15px;
  text-align: center;
  width: 80px;
  padding-left: 15px;
  padding-right: 15px;
}
.action button:last-child{
  margin-right: 0;
}
.addCondition {
  padding: 5px;
}
.addOption {
  line-height: 1;
}
.fieldConditionWrapper {
  display: flex;
  padding: 5px;
}
.fieldCondition{
  display: inline-block;
  margin-top: 5px;
}
.operationText {
  display: inline-block;
  padding: 3px 0;
}
.conditionOptions{
  display: inline-block;
  padding: 0 10px;
  text-align: right;
}
.conditionValueWrapper{
    display: flex;
    margin: 5px 0;
}
.conditionValue {
  display: inline-block;
  position: relative;
}
.closeIcon {
  display: inline-block;
  position: absolute;
  top: 10px;
  padding-left: 10px;
  cursor: pointer;
}
.conditionOptionsWrapper {
  display: inline-block;
}
.input {
  padding: 5px 5px 4px;
  width: 180px;
}


