/* Stylesheet for Checkbox*/
.container {
  display: inline-block;
}
.innercontainer {
  display: inline-block;
  position: relative;
  color: #666;
  padding-left: 18px;
}
.innercontainer.disable {
  pointer-events: none;
}
.innercontainer i {
  display: inline-block;
  width: 14px;
  height: 14px;
  position: absolute;
  left: 0px;
  top: 50%;
  margin-top: -7px;
}

.selected {
  background-image: url('./03-min.png');
  cursor: pointer;
}

.innercontainer:hover>.selected, .checkbox:focus + .selected{
  background-image: url('./05-min.png');
}

.unselected {
  background-image: url('./01-min.png');
  cursor: pointer;
}

.innercontainer:hover>.unselected, .checkbox:focus + .unselected {
  background-image: url('./06-min.png');
}

.checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.horizontal {
  display: inline-block;
  margin-right: 10px;
}
.vertical {
  display: block;
}

.innercontainer input[type='Checkbox']:disabled ~ .text {
  opacity: 0.6;
  pointer-events: none;
}



