.agentsSection{
    position: absolute;
    width: 100%;
    height: 100%;
    border: 1px solid #ddd;
    border-left-width: 0px;
    border-top: 0px;
    border-bottom: 0px;
    overflow-y: auto;
}
.agentitem{
  display: flex;
  height:30px;
  line-height: 30px;
  color: #888;
  cursor: pointer;
  align-items: center;
  padding-right: 30px;
}
.selected{
  line-height: 50px;
  background: #cce7f5 !important;
  height:50px;
  color: #000;
}

.agentitem:hover{
  background: #e6e6e6;
}
.agentitem i{
  flex: 0 0 16px;
  margin-left: 25px;
}
.agentitem .unreadmessage{
  /*float: right;*/
  /*margin: 7px 10px 0px 0px;*/
  flex: 0 0 16px;
  position: absolute;
  right: 10px;
}
.agentitem .description{
  flex:1px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-left:10px;
  display: block;
}
/*.agentitem.selected .unreadmessage, .agentitem.selected i{
  margin-top:17px;
}*/
.departmentsection{
  position: relative;
}
.departmentitem{
  height: 30px;
  line-height: 30px;
  cursor: pointer;
}
.departmentitem:hover{
  background: #f5f5f5;
}
.departmentsection.selectedDpt .departmentitem{
  height: 50px;
  line-height: 50px;
}
.departmentsection.selectedDpt .departmentitem{
  background: #cce7f5 !important;
}
.departmentsection.selectedDpt .departmentitem .description span:first-child,
.selected .agentName {
  font-family: "SourceSans Pro Bold";
}
.departmentsection:hover .departmentitem{
  background: #f5f5f5;
}
.departmentsection div.listsection{
  display: none;
}
.departmentsection.opened div.listsection{
  display: block;
}
.departmentsection i.depSpread{
  transform: rotate(-90deg);
  color: #888;
  position: relative;
  top: 3px;
}
.departmentsection.opened i.depSpread{
  transform: rotate(0deg);
  position: relative;
  top: 3px;
}
.departmentitem{
  display: flex;
}
.departmentitem .icon{
  flex: 0 0 15px;
  margin-left: 10px;
}
.departmentitem .description{
  flex: 1;
  margin-right: 10px;
  margin-left: 5px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  color: #666;
}
.departmentsection.selectedDpt .departmentitem .description,
.departmentsection.selectedDpt .departmentitem i.depSpread
{
  color: #333;
}
.departmentNum, .agentDescription {
  padding-left: 5px;
  font-size: 14px;
  color: #888;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.agentsSection .departmentsection:first-child .departmentitem{
  height: 47px;
  padding-top: 17px;
}
.agentsSection .departmentsection:first-child.selectedDpt .departmentitem{
  height: 57px;
  padding-top: 7px;
}
.agentName {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-left: 5px;
}

.chatContainer {
  display: flex;
  flex-flow: column;
  margin-right:10px;
  height: 100%;
}
.chatContainer>div{
  position: relative;
}
.headerSection{
  margin: 15px 10px 10px 0;
}
.messagesSection{
  flex: 3;
}
.messagesSection>div{
  height:100%;
}
.toolbarSection{
  position: absolute;
  bottom: 175px;
  width: 100%;
  height:40px;
}
.inputSection{
  flex:1;
}
.inputSection>div{
  top: 0px;
}
.headerSection .avatar{
  float: left;
  height: 50px;
}
.headerSection .toolbar{
  float:right;
}
.headerSection .avatar img{
  width: 50px;
  height: 50px;
  border: 1px solid #ddd;
}
.headerSection .avatar span{
  margin-left: 15px;
  line-height: 50px;
  display: block;
  float: right;
  max-width: 480px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 16px;
  font-family: 'SourceSans Pro Bold', Arial;
}
.headerSection .toolbar .commonbtn{
  float: left;
  margin: 19px 0px 0px 10px;
}
.headerSection .toolbar button{
  margin-right: 10px;
  padding: 4px 15px;
  font-size: 14px;
}

.headerSection .toolbar button.disable{
  opacity: 0.5;
}
.noDataSection{
  position: absolute;
  height: 100%;
  width:100%;
  background-repeat: repeat;
  background-image: url('../Image/background.jpg');
}
.noDataSection span{
  color: #329fd9;
  cursor: pointer;
  padding-left: 5px;
}
.noDataSection span:hover{
  text-decoration: underline;
}
.noDataSection > div {
  position: absolute;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.noDataSection h1{
  padding: 0px;
  margin: 0px;
}
.chatWindowContainer{
  position: absolute;
  top: 75px;
  left: 0;
  right: 10px;
  bottom: 10px;
}



