.uploadProgressContainer{

}
.uploadProgress{
  position: absolute;
  bottom: 41px;
  left: 1px;
  width: calc(100% - 2px);
  font-size: 14px;
  color: #000;
  background: #f2f2f2;
  padding: 2px 12px 3px;
}
.uploadProgressPercent{
  color: #888;
  position: relative;
  top: -1px;
}
.uploadProgressBarContainer{
  position:relative;
  display: inline-block;
  margin-right: 10px;
  border-radius: 10px;
  width: 100px;
  height: 10px;
  border:1px solid #cdcdcd;
  overflow: hidden;
}
.uploadProgressBar{
  position: absolute;
  top:0;
  left:0;
  display: inline-block;
  height: 10px;
  background: #329fd9;
}
.cancelUpload{
  top: 4px;
  margin-left: 20px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1;
  color: #666;
  /*text-shadow: 0 1px 0 #fff;*/
}
.cancelUpload:focus, .cancelUpload:hover {
  color: #000;
  text-decoration: none;
  cursor: pointer;
  filter: alpha(opacity=50);
  opacity: .5;
  outline: none;
}
button.cancelUpload{
  -webkit-appearance: none;
  padding: 0;
  cursor: pointer;
  background: 0 0;
  border: 0;
}
.uploadFileName{
  display: inline-block;
  position: relative;
  top: 4px;
  max-width: 70%;
  margin-right: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}



