.CannedMessageContainer {
    width: 100%;
    height: 100%;
    user-select: none;
    overflow: auto;
    position: absolute;
}
.tree {
  font-size: 15px;
}
.container {
    margin: 14px 15px 10px 15px;
    line-height: 1;
}
.container a {
    cursor: pointer;
    font-size: 14px;
}

.searchInput {
  margin-top: 12px;
  position: relative;
}

.tooltip{
  padding: 4px 20px;
  font-size: 12px;
  max-width: 300px;
  font-style: normal;
  word-wrap: normal;
  word-break: break-all;
  text-decoration: none;
  text-transform: none;
}
.tooltip span{
  text-align: left;
  text-transform: none;
}
.inputWidth {
  border: 1px solid #ccc;
  padding: 5px 22px 5px 10px;
  border-radius: 2px;
  transition: border-color ease-in-out 0.2s;
  background-color: #fff;
  font-size: 15px;
  font-family: "SourceSans Pro";
  width: 100%;
}
.inputWidth:focus {
  border-color: #329FD9;
  outline: none;
}
.CannedMessageContainer input::-webkit-input-placeholder {
  color: #666;
  font-style: italic !important;
}
.Loading {
  padding-top: 10px;
}
.search {
  margin: 5px 0;
}
.contentWrapper {
  position: absolute;
  bottom: 0;
  top: 160px;
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  color: #666;
}
.searchItem {
  display: block;
  padding: 7px 15px 10px;
  margin: 1px 0;
}
.searchItem:hover {
  background: #e6e6e6;
}
.searchItem:active {
  background: #aaa;
}
.title {
  font-family: "SourceSans Pro Bold";
  width: 100%;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space:nowrap;
  color: #000;
}
.content {
  display: inline-block;
  line-height: 20px;
  max-height: 40px;
  overflow: hidden;
  word-break: break-word;
  position: relative;
  top: -2px;
}
@supports (-webkit-line-clamp: 2) {
    .content {
        max-height: 40px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        white-space: normal;
    }
}
.searchItem.select {
  background: #cce7f5;
}
.noSearchResult {
    margin-left: 15px;
    color: #666;
}
.closeIcon {
  color: #888;
  font-size: 10px;
  position: absolute;
  right: 10px;
  top: 10px;
  visibility: hidden;
  cursor: pointer;
}
input.inputWidth:focus + .visible.closeIcon {
  visibility: visible;
}
input[type="text"]::-ms-clear{
    display:none;
}
.visible {
  right: 10px;
  top: 10px;
}
input:focus::-webkit-input-placeholder { color:transparent; }
input:focus:-moz-placeholder { color:transparent; } /* FF 4-18 */
input:focus::-moz-placeholder { color:transparent; } /* FF 19+ */
input:focus:-ms-input-placeholder { color:transparent; } /* IE 10+ */


