.ModalClass {
  position: relative;
  background-color: #fff;
  padding: 30px 30px 40px;
  border-radius: 4px;
  outline: none;
}
.ModalClass h3{
  margin: 0 0 15px;
  font-size: 18px;
  line-height: 1;
  text-align: center;
  font-family: "SourceSans Pro Bold";
}
p{
  margin: 0;
}
.close{
  position: absolute;
  top:10px;
  right: 10px;
  font-size: 24px;
  font-weight: 700;
  line-height: 1;
  color: #888;
  text-shadow: 0 1px 0 #fff;
  filter: alpha(opacity=20);
  /*opacity: .2;*/
  font-size: 8px;
  outline: none;
}
.close i{
  font-size: 10px
}
.close:focus, .close:hover {
  text-decoration: none;
  cursor: pointer;
  outline: none;
}
.close:hover {
  color: #969696;
}
.close:focus{
  color: #606060;
}

button.close{
  -webkit-appearance: none;
  padding: 0;
  cursor: pointer;
  background: 0 0;
  border: 0;
}

.OverlayClass {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .45);
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ModalClass .action{
  margin-top: 20px;
  text-align: center;
}
.ModalClass .action button{
  margin-right: 15px;
  text-align: center;
  min-width: 80px;
  padding-left: 15px;
  padding-right: 15px;
}
.ModalClass .action button:last-child{
  margin-right: 0;
}
