.flexContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-repeat: repeat;
  background-image: url('../Image/background.jpg');
}

.container h1 {
  margin: 0;
}

.container div {
  line-height: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
}

.container div a {
  padding-right: 5px;
  cursor: pointer;
}

.popupicon {
  position: fixed;
  top: 60px;
  right: 5px;
}
.childpopupicon {
  position: fixed;
  top: 5px;
  right: 5px;
}


