.info {
    width: 100%;
    height: 100%;
    word-wrap: break-word;
    word-break: break-all;
    text-overflow: ellipsis;
    user-select: none;
    overflow: auto;
    position: absolute;
}
.Container {
    font-size: 14px;
    width: 100%;
    padding: 13px 15px;
}
.featrue {
    font-size: 14px;
    width: 100%;
    padding: 13px 15px 0;
}
.Container .editIconWrapper{
    position: relative;
}
.title {
    color: #888;
    margin-bottom: 5px;
    line-height: 1;
}
.title h3{
    color: #333;
    margin: 0;
    font-size: 15px;
    line-height: 18px;
    padding-right: 14px;
    font-family: "SourceSans Pro Bold";
    word-break: normal;
}
.line input {
    color: #666;
}
.line {
    color:#666;
    display: block;
    margin-top: 5px;
    user-select: none;
    font-size: 15px;
    line-height: 18px;
}
.line:focus {
    user-select: text;
}
.line a, .line i{
    user-select: none;
}
.line.customVariable a{
  user-select: text;
}
.line:first-child, .title + .line{
  margin-top: 0;
  font-size: 15px;
}
.line i{
  float: left;
  margin-top: 2px;
  margin-right: 10px;
}
.line .select i {
  float:none;
}
.Container a{
    text-decoration: none;
    color: #32afd9;
    cursor: pointer;
    line-height: 18px;
}
.Container a:hover{
    text-decoration: underline;
}
.editIcon{
    position: absolute;
    right: 0;
    top:0;
    display: none;
}
.editIcon:hover {
    cursor: pointer;
}
div.Container.editable.notEdit:hover {
    background: #e6e6e6;
}
div.Container.editable.notEdit:hover .editIcon{
    display: inline-block;
}
.seperator {
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px;
    font-size: 16px;
    color: #bbb;
    user-select: none;
}

.inlineBlock {
    display: inline-block;
}
.osIconWrapper {
    margin-bottom: 10px;
}
.osIconWrapper span{
   display: inline-block;
   margin-right: 10px;
}
.arrowIcon{
    padding: 0 5px;
    cursor: pointer;
    position: relative;
    top: 4px;
}
i.segments{
    height: 15px;
    width: 8px;
    display: inline-block;
    padding-right: 5px;
    border: 1px solid rgba(0,0,0,.2);
}
.segmentWrapper {
    margin-bottom: 5px;
    width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.segmentWrapper span{
    padding-top: 2px;
}
.input {
    width: 100%;
}
.input div{
    padding: 6px 0 4px;
}

.input input{
    padding: 8px 0 2px;
    width: 85%;
}

.marginRight20 {
  margin-right: 20px;
}
.buttonWrapper {
  padding-top: 5px;
  text-align: center;
}
.preInfoEdit {
  margin-top: 15px;
}
.select {
    width: 85%;
    display: inline-block;
}

.errorMessage {
  text-align: center;
  word-wrap: break-word;
  word-break: break-all;
  width: 100%;
}
.errorMessage span{
    background-color: #dc5f5f;
    line-height: 2;
    padding: 5px 15px;
    color: #fff;
    font-size: 11px;
    border-radius: 2px;
}
.preIConWrapper {
    padding: 5px 0 0 0;
    float: left;
}
.popmenuWrapper {
    display: none;
    position: relative;
    z-index: 999;
}
.popmenu {
    position: absolute;
    width: 100px;
    top: 5px;
    left: -20px;
    border: 1px solid #bbb;
    background: #fff;
}
.popmenu li{
    padding: 4px 10px;
}
.popmenu li:hover{
    background: #eee;
    cursor: default;
}
.popmenu li.active{
    background: #d5ebf6;
}
.arrowIconWrapper {
    display: inline-block;
}
.arrowIconWrapper:focus {
    outline: none;
}
.arrowIconWrapper:focus + .popmenuWrapper{
    display: inline-block;
}
.commentTextarea {
    width: 100%;
}
.commentEditIcon {
    top: -20px;
    right: -10px;
}
.okButton {
    padding-left: 20px;
    padding-right: 20px;
    margin-right: 20px;
}

.copyArea {
    z-index: -1;
    opacity: 0;
    position: absolute;
    left: 0;
}
.wherefromMenuWrapper {
    display:none;
    position: fixed;
}
.wherefromMenu {
    position: absolute;
    width: 150px;
    border: 1px solid #bbb;
    background: #fff;
}
.wherefromMenu a {
    color: #000;
    width: 100%;
    display: inline-block;
}
.wherefromMenu li{
    padding: 2px 10px;
}
.wherefromMenu li:hover{
    background: #eee;
    cursor: pointer;
}

.tooltip{
  padding: 4px 8px!important;
  font-size: 12px!important;
  text-align: left!important;
  font-style: normal;
  max-width: 300px;
  word-wrap: normal;
  word-break: break-all;
}
.tooltip :global(.multi-line){
  text-align: left!important;
}
.currentBrowsingLink {
    display: block;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    display: block;
}
/*@supports (-webkit-line-clamp: 2) {
    .currentBrowsingLink {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        white-space: normal;
    }
}*/
.wherefromDiv:focus .wherefromMenuWrapper {
    display: block;
}
a.wherefromLink:focus + .wherefromMenuWrapper {
    display: block;
}
.wherefromLink {
    display: inline-block;
}
.wrapper {
    position: relative;
}
.wrapper i {
  top: 0;
}
.icon {
    position: absolute;
    bottom: 3px;
}
.text{
    display: inline-block;
    padding-left: 25px;
}
.customfiled input {
    color: #666;
}


