/* Stylesheet for Image*/
.logo {
    width: auto;
    height: 28px;
    display: block;
}
a:link, a:visited {
    text-decoration: none;
}
.none {
  display: none;
}
.logo_imageWrapper{
    margin-right: 25px;
    display: inline-block;
    width: 152px;
    height: 28px;
    cursor: pointer;
    background-size: 152px 28px, cover;
}

.logo_seperator{
  border-right: 1px solid;
}

.logo_text {
    position: relative;
    top: -9px;
    font-size: 13px;
    margin-left: 20px;
}



