.modelHeight {
  height: 598px;
}
.modelButtons {
  bottom: 30px;
  /*padding: 0 33%;*/
  width: calc(100% - 60px);
  position: absolute;
}
.modelButtons button:last-child {
  margin-left: 15px;
}
.settingsContent {
  padding-top: 20px;
  width: 640px;
  color: #666;
  line-height: 26px;
}
.configCols {
  float: left;
  display: flex;
}
.rightCols {
  margin-left: 10px;
}
.fullWidth {
  width: 100%;
}
.inputWidth {
  width: 200px;
  color: #666;
  padding: 4px 5px;
}
.preDefinedMessage {
  margin-left: 20px;
  margin-top: 5px;
}
.preDefinedMessage textarea {
  width: 100%;
}
.selectLabel {
  left: 5px;
  text-align: left;
}
.settingsContent td:first-child {
  text-align: left;
}
.button{
  border: 0px;
  color: #666;
  padding:0px 10px;
}
.button:active {
  background-color: #ffffff;
  border: 0px;
}
.settingsContent li {
  padding-bottom: 5px;
  height: 35px;
}
.soundSettings table {
  border-collapse: collapse;
  border-spacing: 0px;
  border: none;
  width: 100%;
}
.soundSettings th:first-child {
  border-top-left-radius: 2px;
}
.soundSettings th:last-child {
  border-top-right-radius: 2px;
}
.soundSettings th,
.soundSettings td {
  border-right: 1px solid #fff;
}
.soundSettings th:last-child,
.soundSettings td:last-child {
  border-right: none;
}
td.soundConfig {
  padding-left: 15px;
  padding-right: 20px;
  /*line-height: 100%;*/
  display: flex;
  align-items: center;
}
.soundSettings th {
  height: 40px;
  padding: 5px 15px;
  background: #e5e5e5;
  text-align: center;
  font-family: inherit;
  font-weight: normal;
}
.soundSettings th:first-child {
  text-align: left;
  padding-left: 15px;
}
.soundSettings tr {
  height: 40px;
}
.soundTitle {
  padding-left: 15px;
  padding-right: 20px;
}
.soundSettings tr:nth-child(2n + 1) {
  background: #f0f0f0;
}
.soundSettings tr:nth-child(2n) {
  background: #fafafa;
}
.soundSettings tr:last-child td:first-child {
  border-bottom-left-radius: 2px;
}
.soundSettings .select {
  border: none !important;
  background-color: transparent;
  outline: none;
}
.soundSettings .select:hover {
  border: none;
}
.soundSettings .select.active,
.soundSettings .select select,
.soundSettings .selectDropDownList {
  border: none;
}
.soundSettings .selectLabel {
  padding-right: 10px;
}
.soundPlayIcon{
  display: block;
  width: 16px;
  height: 16px;
  margin: 3px 0px 0px 0px;
  float: left;
}
.soundItem {
  float: left;
  display: flex;
}
.soundIconBtn {
  margin-top: 8px;
}
.columnsTitle {
  line-height: 1;
}

input[type=range] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  background: transparent;
  cursor: pointer;
  padding: 6px 0;
  height: 30px;
  overflow: hidden;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 1px solid #cdcdcd;
  height: 16px;
  width: 16px;
  border-radius: 12px;
  background: #fff;
  cursor: pointer;
  margin-top: -8px;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  background: #329fd9;
  height: 1px;
  cursor: pointer;
}

input[type=range]::-moz-range-thumb {
  border: 1px solid #cdcdcd;
  height: 16px;
  width: 16px;
  border-radius: 12px;
  background: #fff;
  cursor: pointer;
  margin-top: -8px;
}

input[type=range]::-moz-range-track {
  width: 100%;
  margin-top: 8px;
  height: 1px;
  cursor: pointer;
  background: #cdcdcd;
}

input[type=range]::-moz-range-progress {
  height: 1px;
  background: #329fd9;
  margin-top: 8px;
  border: 0;
}

input[type=range]::-ms-track {
  background: transparent;
  cursor: pointer;
  border: 0;
  border-color: transparent;
  border-radius: 0;
  border-width: 0;
  margin-top: 0;
  color: transparent;
  height: 1px;
  width: 100%;
}

input[type=range]::-ms-thumb {
  border: 1px solid #cdcdcd;
  height: 16px;
  width: 16px;
  border-radius: 12px;
  background: #fff;
  cursor: pointer;
  margin-top: 0 !important;
}

input[type=range]::-ms-fill-lower {
  background: #329fd9;
}

input[type=range]::-ms-fill-upper {
  background: #cdcdcd;
}

.disable input[type=range]::-webkit-slider-runnable-track {
  background: #cdcdcd;
  content: '';
  pointer-events: none;
}
.disable input[type=range]::-ms-fill-lower {
  background: #cdcdcd;
}
.disable input[type=range]::-moz-range-progress {
  background: #cdcdcd;
}

.restoreButton {
  width: 130px !important;
  position: absolute;
  left: 0px;
}

.shortInput {
  width: 35px;
  margin-left: 5px;
  margin-right: 5px;
  padding: 3px 5px;
}

.screenSharing {
  padding-top: 5px;
}
.screenSharingTitle {
  padding-bottom: 5px;
  font-family: "SourceSans Pro Bold";
}
.screenSharing .loading {
  position: relative;
  top: 2px;
}
.screenSharing .testMsg {
  display: inline-block;
}
li {
  height: auto;
}
.screenSharing li .title {
  width: 80px;
  color: #888;
  display: inline-block;
  box-sizing: border-box;
}
.title {
  padding-left: 10px;
  padding-right: 20px;
  color: #888;
}
.linkBtn {
  color: #329fd9;
  padding-left: 10px;
  cursor: pointer;
}
.linkBtn:hover {
  text-decoration: underline;
}
.loadingIcon{
  display: block;
  width: 16px;
  height: 16px;
  margin: 3px 0px 0px 0px;
  float: left;
}
.successMsg {
  color: #74b75d;
  margin-left: 10px;
}
.errorMsg {
  color: #dc5f5f;
  margin-left: 10px;
}
.joinMeInfo {
  padding-left: 10px;
  color: #888;
}
.validationError {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(235, 79, 67, 0.9);
  width: 220px;
  border-radius: 8px;
  padding: 10px 20px;
  text-align: center;
  color: #fff;
}
.soundSelector {
  margin-right: 15px;
  padding-top: 3px;
}
.soundSelector div,
.soundSelector span {
  border: none;
}
.soundSelector span:first-of-type {
  padding-right: 10px;
}



