/***for promptnumber***/
.promptNumber{
  background: #fe8e14;
  border-radius: 50%;
  text-align: center;
  width: 15px;
  height: 15px;
  display: inline-block;
  color: #fff;
  line-height: 15px;
  font-size: 11px;
  font-style: normal;
  transition: display ease-in 1s;
}
.promptDisplay{
  display: inline-block;
}
.statusIcon{
  display:block;
  background: url('./images/status_online.png') no-repeat;
  width: 16px;
  height: 16px;
  float: left;
  padding-right:8px;
}

.cover{
  position: absolute;
  display: none;
}
.cover.coverOpen{
  margin-top: -23px;
  margin-left: 10px;
  width: 25px;
  height: 20px;
  opacity: 0.1;
  display: block;
  z-index: 2;
}
.fontList{
  position: absolute;
  background: #fff;
  font-size: 14px;
  border:1px solid #ddd;
  display: none;
}
.fontList.fontOpen{
  display: block;
  z-index: 2;
}
.fontList li{
  height: 25px;
  line-height: 25px;
}
.fontList li a{
  display: block;
  padding: 0 10px;
  text-decoration: none;
  color:#666;
  cursor: pointer;
}
.fontList li.fontActive{
  background-color: #cce7f5!important;
}
.fontList li:hover{
  background-color: #ededed;
}
.fontList li.fontActive a {
  cursor: default;
}
.promptHide{
  display: none;
}
