.error{
  position: absolute;
  bottom: 41px;
  left: 1px;
  width: calc(100% - 2px);
  font-size: 14px;
  color: #ec4145;
  background-color: #ffe6dd;
  height: 30px;
  line-height: 30px;
  padding-left: 12px;
  padding-right: 12px;
}
.error .errorContent{
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: calc(100% - 60px);
}
.error a:link{
  position: relative;
  top: -10px;
  margin-left: 15px;
  text-decoration: underline;
}



