.agenttabwraper{
    position: absolute;
    /*top: 50px;
    left: 0px;
    bottom: 0px;*/
    /*float: left;
    width: 50px;*/
    flex-basis: 50px;
    user-select: none;
    border-right: 1px solid #ddd;
    z-index: 1;
}
.ultab{
    list-style: none;
    width:50px;
    padding:0px;
    margin: 0px;

}
.tabItem:first-child{
    margin-top:40px;
}
.content{
    position: absolute;
    top: 0px;
    left: 50px;
    right:0px;
    bottom: 0px;
    background: #fff;
    display: none;
}
.tabItem{
    height: 48px;
    width: 100px;
    text-align: center;
    transform: rotate(-90deg);
    margin-left: -24px;
    margin-top: 60px;
    color: #333;
    font-size: 15px;
    line-height: 47px;
    border-bottom: 2px solid #fff;
    -webkit-user-select: none;
    user-select: none;
    cursor: pointer;
}
.tabItem:hover{
    color: #329fd9;
}
.selected{
    border-bottom: 3px solid #329fd9;
    color: #329fd9;
    font-family: "SourceSans Pro Bold";
}
.info{
    position: absolute;
    top: 18px;
    right: 0;
    color: #fff;
    transform: rotate(90deg);
    pointer-events: none;
    font-style: normal;
}
.tabDisplay{
    display: block;
}
.refresh{
  text-decoration: none;
  color: #32afd9;
  cursor: pointer;
  font-size: 14px;
}
.loading{
  color:#666666;
  font-size:14px;
  top: 15px;
  left: 15px;
}



