.chatitemcontainer{
  width: 100%;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  min-height: 55px;
  position: relative;
  background: #fff;
  cursor: pointer;
  box-sizing: border-box;
}
.acceptStyle{
  background-color: #fedb9e !important;
  border-bottom: 1px solid #feedce;
}
.selected{
  background: #cce7f5 !important;
}
.chatitemcontainer:hover{
  background-color: #e6e6e6;
}
.captureitem{
  -webkit-flex: 0 0 18px;
  flex: 0 0 18px;
}
.captureitem i{
  margin:5px;
}
.statusitem{
  -webkit-flex: 0 0 35px;
  flex: 0 0 34px;
}
.statusitem i{
  margin: 11px 8px 0px 5px;
}
.detailitem{
  -webkit-flex: 1;
  flex: 1;
  overflow:hidden;
  position: relative;
  min-height: 55px;
}
.infoitem{
  display:flex;
  align-items: center;
  -webkit-flex: 0 0 40px;
  flex:  0 0 40px;
  padding-left: 5px;
}
.segmentitem{
  -webkit-flex:  0 0 10px;
  flex:  0 0 10px;
}
.pinnStyle{
  display: inline-block;
  width:12px;
  height: 12px;
  opacity: 0;
}
.pinned{
  color: #329fd9;
  opacity: 1;
}
.chatitemcontainer:hover .pinned{
  color: #329fd9 !important;
  opacity: 1;
}
.chatitemcontainer:hover .nopinned{
  color: #b6b6b6 !important;
  opacity: 1;
}
.chatitemcontainer:hover .deleteIcon{
  opacity: 1;
}
.chatitemcontainer.nobutton {
  padding: 0px;
}
.chatitemcontainer.nobutton .statusitem i,
.chatitemcontainer.nobutton .chatItemDetail{
  margin-top:0px;
  position: relative;
  top: 20px;
}
.chatitemcontainer.nobutton .chatItemPrompt{
  /*right: 11px;*/
}
.chatItemDetail{
  font-size: 15px;
  margin: 10px 0px 0px;
  width: 100%;
}
.visitorName{
  color:#333;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  line-height: 16px;
}
.selected .visitorName{
  font-family: "SourceSans Pro Bold";
}
.visitorDepartment{
  color: #969696;
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.refuseBtn,.refuseBtn:active{
  border: 0px;
  background: none;
  margin-right: 0px;
  padding: 0px 0px 0px 15px;
  line-height: 17px;
  height: 17px;
  text-decoration: underline;
  font-size: 14px;
}
.acceptBtn,.wrapupbtn{
  line-height: 1;
  margin-right: 0px;
  padding: 0px 10px 2px;
  font-size: 14px;
  height: 22px;
}
.wrapupbtn, .actioncontainer{
  margin: 5px 0px 12px;
}
.promptNumber{
  font-style: normal;
  font-size: 10px;
}
.chatItemSegment{
  position: absolute;
  top: 50%;
  right: 0px;
  transform: translateY(-50%);
}
.chatItemSegment i{
  display: block;
  font-size: 0px;
  width:5px;
  float: right;
  clear: both;
  border: 1px solid rgba(0,0,0,.2);
}
.deleteIcon{
  color: #888;
  opacity: 0;
}
/********* config style *********/
.chatItemConfig{
    border: 1px solid #ddd;
    border-radius: 5px;
    position: absolute;
    background: #fff;
    color: #666;
    bottom: -5px;
    font-style: normal;
    left: 20px;
    z-index: 9999;
    display: none;
    text-align: left;
    font-family: "SourceSans Pro", Arial;
    pointer-events: painted;
}
.configitem{
  visibility: hidden;
  width: 13px;
  height: 13px;
  padding-right:20px;
  text-align: left;
}
.selectedicon{
  visibility: visible;
  color: #329fd9;
}
.chatconfigpanel{
  list-style: none;
  padding: 13px 0px 0px;
  margin: 0px;
}
.chatconfigpanel li{
  margin-bottom: 15px;
  padding: 0px 20px 0 15px;
  cursor: pointer;
  width: 236px;
}
.chatconfigpanel li:last-child{
  padding-top: 13px;
  border-top: 1px solid #ddd;
}
.chatItemConfig:after,
.chatItemConfig:before {
    content: '';
    display: block;
    position: absolute;
    bottom: 5px;
    width: 0;
    height: 0;
    border-style: solid;
}
.chatItemConfig:after {
    left: -12px;
    border-color: transparent #ddd transparent  transparent;
    border-width: 6px;
    z-index: 999;
}
.chatItemConfig:before {
    left: -10px;
    border-color: transparent #fff transparent transparent;
    border-width: 6px;
    z-index: 1000;
}
/********* end config style *********/
.itemContainer{
  position: absolute;
  width: 100%;
  height: 100%;
  border: 1px solid #ddd;
  border-left-width: 0px;
  border-top:0px;
  border-bottom: 0px;
  user-select: none;
}
.configPanel{
  position: absolute;
  bottom: 0px;
  width: 100%;
  background: #fff;
}
.configPanel > div{
  cursor: pointer;
  margin: 0px 0px 10px 20px;
}
.configicon{
  position: relative;
  outline-style: none;
  margin: 0px;
  color:#666;
  cursor: pointer;
}
.configicon:hover,.configicon:focus{
  color:#329fd9;
}
.configicon:focus{
  pointer-events: none;
}
.configicon:focus .chatItemConfig{
  display: block;
}
.chatItemConfig:hover {
  display: block;
}

.acceptStyle .idleStyle {
  line-height: 1;
}
.idleStyle{
  color: #333;
  display: block;
  clear: both;
  line-height: 1;
}
.idleUnderProptNum{
  margin-top: 0;
}
.chatItemPrompt{
  text-align: center;
  line-height:0;
}
.chatItemPrompt i.deleteIcon{
  position: relative;
  margin-top: 3px;
}
.itemContainer > .showChatViaList,.showChatViaGroup {
  position: absolute;
  right: 0px;
  bottom: 45px;
  top: 0px;
  left: 0px;
  overflow-y: auto;
}
.showChatViaGroup .showChatViaList {
  display: none;
}
.groupNameWraper{
  background: #f0f0f0;
  font-size: 14px;
  padding: 5px;
  height: 35px;
  overflow: hidden;
  color: #666;
}
.chatItemConfig li:hover{
  color: #329fd9;
}
.groupSection{
  position: relative;
  overflow: hidden;
}
.groupNameWraper > span{
  display: block;
  position: relative;
  top: -12px;
  left: 20px;
  color: #666;
}
.groupSpread{
  display: block;
  position: relative;
  top: 6px;
  width: 16px;
}
.groupNameWraper.opened + .showChatViaList{
  display: block;
}
.groupNameWraper > i{
  transform: rotate(-90deg);
}
.groupNameWraper.opened > i{
  transform: rotate(0);
}
.noChatSection{
  position: absolute;
  height: 100%;
  width:100%;
  background-repeat: repeat;
  background-image: url('../Image/background.jpg');
}
.noChatSection span{
  color: #329fd9;
  text-decoration: none;
  cursor: pointer;
}
.noChatSection span:hover{
  text-decoration: underline;
}
.noChatSection > div {
  position: absolute;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.noChatSection h1{
  padding: 0px;
  margin: 0px;
}



