.chatAction{
  position: absolute;
  bottom: 175px;
  width:100%;
	height: 40px;
}
.chatAction > ul > li{
	float: left;
}
.chatAction > ul > li .chatActionIcon{
	display: inline-block;
  margin-left: 10px;
	margin-top: 10px;
	height: 20px;
	padding: 2px 5px;
  border-radius: 4px;
  cursor: pointer;
  color: #333;
  background: none;
}
.chatAction > ul > li .chatActionIcon > i{
  vertical-align: top;
}
.chatAction > ul > li .chatActionIcon:hover, .chatAction > ul > li .chatActionIcon.iconActive{
  background: #cce7f5;


}
:global(.rc-upload){
  display: inline-block;
  /*margin-left: 10px;
  margin-top: 10px;*/
  height: 20px;
  padding: 2px 5px;
  border-radius: 4px;
  cursor: pointer;
  outline: none;
}
:global(.rc-upload):hover{
  background: #cce7f5;
}
.chatActionIconDisabled{
  /*pointer-events: none;*/
  background: #fff!important;
  cursor: default!important;
  color: #ccc;
}
.chatActionIconDisabled i, :global(.rc-upload-disabled){
  opacity: .2;
  cursor: default!important;
  background: #fff!important;
}

.chatAction > ul > li img{
	display: block;
}



