.field {
  margin-bottom: 15px;
}
.required {
  padding: 0 0 0 5px;
  color: #d34747;
}
.inputWidth {
  width: 100%;
}
.field .radio label{
  margin-bottom: 1px;
}
.fieldTitle{
  font-size: 14px;
  color: #888;
  margin-bottom: 2px;
}
.marginRight20 {
  margin-right: 20px;
}
.buttonWrapper {
  padding-top: 5px;
  text-align: center;
}
.okButton {
    padding-left: 20px;
    padding-right: 20px;
    margin-right: 20px;
}
.groupdesc {
  cursor: pointer;
  color: #888;
}
.groupdesc .arrow {
  display: block;
  float: left;
  width: 10px;
  margin-right: 5px;
}
.groupdesc .arrow i{
  display: block;
  float: left;
  margin: 5px 0px 0px 0px;
  transform: rotate(-90deg);
}
.groupdesc.opened .arrow i{
  margin: 5px 0px 0px 0px;
  transform: rotate(0);
}
.item {
  width: calc(100% - 28px);
  display: block;
  margin-left: 15px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.item span {
  width: calc(100% - 20px);
  display: block;
  line-height: 20px;
  float: right;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}


