.Container{
  position: fixed;
  z-index: 10;
  background-color: transparent;
  border-radius: 4px;
}
.buttonTransform{
  bottom: 60px;
}
.fromContainer{
  bottom: 0px;
  left: 20px;
  box-shadow: 0px 0px 10px rgba(0,0,0,.25);
  -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,.25);
  -moz-box-shadow: 0px 0px 10px rgba(0,0,0,.25);
  background-color: #fff;
}
.button{
  width: 45px;
  height: 170px;
  cursor: pointer;
  background: url('./feedback.png');
}
.feedbackIcon{
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
  float: left;
  margin-left: 21px;
  display: inline-block;
  margin-right: 10px;
  height: 18px;
  margin-top: 9px;
}
.feedBackTitle{
  float:left;
}
.formContainer{
  /*border: 1px solid #ccc;*/
  border-radius: 4px;
}
.form{
 width: 300px;
 margin: 30px;
}
.formhead{
  background-color: #fa9423;
  text-align: center;
  line-height: 30px;
  height: 30px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  color: #fff;
  padding-left: 25px;
}
.close{
  float: right;
  margin: 11px 10px;
  font-size: 8px;
  color: #bc6d18;
  cursor: pointer;
}
.close:hover{
  color: #d77235;
}
.close:focus{
  color: #925512;
}
.singleSelect{
  margin: 10px 0;
  color:#333;
  line-height: 17px;
  font-size: 14px;
}
.noteInfo{
  font-size: 14px;
  line-height: 17px;
  color: #888;
}
.problemInfo{
  position: relative;
  border: 1px solid #ccc;
  height: 80px;
}
.txtareafocus {
  border-color: #329fd9;
}
.txtareadisbled{
  background-color: #ebebe4;
}
.txtareaenabled{
  background-color: #fff;
}
.textarea{
  padding: 10px;
  width: 100%;
  height: 58px;
  font-size: 14px;
  line-height: 17px;
  color: #000;
  border: none;
  outline: none;
  overflow-x: hidden;
  overflow-y: auto;
}
.cover{
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0px;
  filter:alpha(opacity=100);
  -moz-opacity:1;
  opacity:1;
  z-index: 2;
}
:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    color: #bbb; line-height: 17px; font-size: 14px;
}
::-moz-placeholder { /* Mozilla Firefox 19+ */
    color: #bbb; line-height: 17px; font-size: 14px;
}
input:-ms-input-placeholder{
    color: #bbb; line-height: 17px; font-size: 14px;
}
input::-webkit-input-placeholder{
    color: #bbb; line-height: 17px; font-size: 14px;
}
.fileattarch{
  position: absolute;
  right: 2px;
  bottom: 3px;
  cursor: pointer;
}
.fileattarched{
  position: absolute;
  left: 5px;
  bottom: 2px;
  font-size: 13px;
}
.fileattarched > i {
  float: left
}
.fileName{
  max-width: 170px;
  float: left;
  margin-left: 6px;
  position: relative;
  top: -2px;
  color: #329fd9;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: default;
}
.fileDelete{
  font-size: 8px;
  margin-left: 5px;
  color: #888;
  cursor: pointer;
  margin-top: 3px;
}
.submit{
  margin: 20px 0px 30px 0;
  text-align: center;
}
.submitButton{
  color:#fff;
  background-color:#fa9423;
  border: 0px solid transparent;
}
.success,
.error{
  font-size: 14px;
  line-height: 14px;
  margin-top: 10px;
  margin-bottom: 15px;
}
.error{
  color: #d34747;
}
.success{
 color: #74b75d;
}
.dn{
  display: none;
}



