.versionPrompt {
  width: 600px;
  padding: 30px;
}
.contaner{
  margin: 0 10px;
}
.banner{
  height: 160px;
  background-color: #ccc;
}
.banner img{
  width: 100%;
  height: 100%;
}
.content{
  color: #333;
  line-height: 17px;
  font-size: 14px;
}
.bottomInfo{
  color: #888;
  font-size: 12px;
  position: absolute;
  height: 42px;
  background-color: #ccc;
  width: 100%;
  margin-left: -30px;
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  padding: 10px 0;
}
.bottomInfo span{
  margin: 0px 30px;
}
.blockTop{
  margin-top: 20px;
}
.arrow{
  transform: rotate(90deg);
  position: fixed;
  bottom: 70px;
  width: 120px;
  height: 150px;
  left: 50px;
  text-align: center;
}
.feedbackbtn {
  position: fixed;
}



