html, body {
  margin: 0;
}

html, body, :global(#app) {
  height: 100%;
}
body {
  /**
   * To make sure when browser is too small, no scrollbar will be shown.
   * Otherwise tabs will be messed up when scrolling in "visitor" panel.
   * See issue 18781
   */
  overflow: hidden;
}
h1, h2, h3 {
  font-weight: normal;
  font-family: "SourceSans Pro Bold";
}
::-webkit-scrollbar-button {
  width: 13px;
  height: 13px;
  background-image: url(./images/scrollbutton-arrow.png);
  background-repeat: no-repeat;
  background-color: #f0f0f0;
}
::-webkit-scrollbar-button:hover{
  background-color:#dadada;
}
::-webkit-scrollbar-button:focus, ::-webkit-scrollbar-button:active{
  background-color: #606060!important;
}


::-webkit-scrollbar-button:vertical:decrement{
  background-position:  center -26px;
}
::-webkit-scrollbar-button:vertical:increment{
  background-position:  center 0;
}
::-webkit-scrollbar-button:horizontal:decrement{
  background-position:  center -91px;
}
::-webkit-scrollbar-button:horizontal:increment{
  background-position:  center -52px;
}
::-webkit-scrollbar-button:vertical:decrement:focus, ::-webkit-scrollbar-button:vertical:decrement:active{
  background-position:  center -39px;
}
::-webkit-scrollbar-button:vertical:increment:focus, ::-webkit-scrollbar-button:vertical:increment:active{
  background-position:  center -13px;
}
::-webkit-scrollbar-button:horizontal:decrement:focus, ::-webkit-scrollbar-button:horizontal:decrement:active{
  background-position:  center -78px;
}
::-webkit-scrollbar-button:horizontal:increment:focus, ::-webkit-scrollbar-button:horizontal:increment:active{
  background-position:  center -65px;
}


::-webkit-scrollbar {
    width: 13px;
    height: 13px;
    background-color: #f0f0f0;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
}
::-webkit-scrollbar-thumb {
    background-color: #cdcdcd;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;
}
::-webkit-scrollbar-thumb:hover {
    background-color: #a6a6a6;
}
::-webkit-scrollbar-thumb:focus, ::-webkit-scrollbar-thumb:active{
    background-color: #606060;
    border: 1px solid #737373;
}
/*::-webkit-scrollbar-thumb:window-inactive{
    background-color: #606060;
    border:1px solid #737373;
}*/
* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box
}

*:before,*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box
}

body{
  margin: 0;
  font-family: "SourceSans Pro", Arial;
  font-size: 15px;
  line-height: 1.42857143;
}

div:focus {
  outline: none;
}

.clearfix:after{
  content:"";
  display:block;
  clear:both;
}

a{
  color: #329fd9;
  text-decoration: none;
}
a:focus,a:hover,a:active {
    text-decoration: underline;
    outline: none!important
}
input,textarea{
  font-family: inherit;
}
ul,ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
b, strong {
  font-family: SourceSans Pro Bold,Arial;
}
/*for tooltip*/
.tooltipInfo{
  font-size: 12px !important;
  padding: 5px 10px !important;
  text-indent: 0px;
  text-transform: capitalize;
  font-family: "SourceSans Pro", Arial;
}
/* disable  capitalize*/
.tooltipInfoNoCapitalize{
  font-size: 12px !important;
  padding: 5px 10px !important;
  text-indent: 0px;
  text-transform: none;
  font-family: "SourceSans Pro", Arial;
}


.popupList{
  position: absolute;
  background: #fff;
  font-size: 12px;
  border:1px solid #ddd;
  display: none;
}
.popupList.open{
  display: block;
  z-index: 2;
}
.popupList li{
  height: 25px;
  line-height: 25px;
}
.popupList li a{
  display: block;
  padding: 0 10px;
  text-decoration: none;
  color:#888;
  cursor: pointer;
}
.popupList li.active{
  background-color: #d4e8f3!important;
}
.popupList li:hover{
  background-color: #ededed;
}
.popupList li.active a {
  cursor: default;
}
.layoutContainer{
  display: flex;
  position: absolute;
  top: 55px;
  left: 0;
  right: 0;
  bottom: 0;
  min-width: 1024px;
  min-height: 450px;
}

.layoutContainer .layoutContent, .childWindow .layoutContent {
  position: absolute;
  left: 50px;
  right: 0;
  bottom: 0;
  top: 0;
  /*position: relative;*/
  overflow-x: auto;
  flex-grow: 1;
}
.childWindow .layoutContent {
  left: 0px;
}

.layoutContainer .chatwindowContainer{
  position: absolute;
  top:75px;
  left: 0;
  right: 10px;
  bottom:10px;
}

.layoutContent .visitorDetailsContainer {
  position: fixed;
  right: 0;
  bottom: 0;
  top: 110px;
  z-index: 1;
  min-width: 350px;
  background-color: #f0f0f0;
  padding: 6px;
  /*box-shadow: -4px 0px 4px 0px rgba(0, 0, 0, 0.3);*/
}
.childWindow .layoutContent .visitorDetailsContainer {
  top: 55px;
}

.visitorDetailsContainer .close {
  display: inline-block;
  width: 22px;
  height: 60px;
  position: absolute;
  top: 50%;
  transform: translate(-22px, -30px);
  background-image: url('./images/close.png');
  background-repeat: no-repeat;
  background-position: center center;
  cursor: pointer;
}

.visitorDetailsContainer .close:hover {
  background-image: url('./images/hover.png');
}

/* textarea is not resizable by default, see issue 4035 */
textarea {
  resize: none;
}
.childWindow {
  position: absolute;
  height: 100%;
  width: 100%;
}
@media screen and (min-width:1024px){
  div.topTab {
    position: fixed;
    top: 69px;
    right: 15px;
  }
  .childWindow div.topTab {
    top: 14px;
  }
}
@media screen and (max-width:1023px){
  div.topTab{
    margin-left: 500px;
    position: absolute;
    top: 14px;
  }
}


