.headSection{
  position: relative;
  width:100%;
  height: 55px;
  min-width: 1024px;
  background:#f5f5f5;
  border-bottom: 1px solid #ddd;
  user-select: none;
}
.headLeft{
    float:left;
}
.headRight{
    float: right;
}
.menuItem{
    float: left;
    font-size: 14px;
    color: #666;
    margin-right: 20px;
    cursor: pointer;
    height: 55px;
    line-height: 55px;
    position: relative;
}
.menuItem:last-child{
    margin-right: 15px;
}
.menuItem label,.menuItem i{
    cursor: pointer;
    width:16px;
    height: 16px;
}
.menuItem i{
    margin-top: 2px;
}
.menuItem>li:last-child{
    margin-right:15px;
}
.menuContainer>ul{
    list-style: none;
    padding: 0px;
    margin: 0px;
}
.menuContainer>ul>li:last-child{
    border-bottom:0px;
}
.item{
    padding: 2px 15px;
    cursor: pointer;
    line-height: 19px;
    border-bottom: 1px solid #ddd;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow:hidden;
    height: 0;
    -webkit-transition: height .2s ease-in, padding .2s ease-in;
    -moz-transition: height .2s ease-in, padding .2s ease-in;
    -o-transition: height .2s ease-in, padding .2s ease-in;
    transition: height .2s ease-in, padding .2s ease-in;
}
.menuItem.mouseover .item {
    height: 30px;
    padding: 5px 15px;
}
.item:hover{
  background-color: #e6e6e6
}
.popupStyle{
    min-width: 120px;
    left: 0px;
    padding-top: 13px;
    opacity: 0;
    z-index: -1!important;
    visibility: hidden;
    transform: translateY(-2em);
}
.menuItem.mouseover .popupStyle {
  opacity: 1;
  visibility: visible;
  transform: translateY(0%);
  z-index: 9999!important;
  transition: all 0.3s ease-in-out 0s;
}
.popupStyle i.statusIcon{
    margin:2px 8px 0px 0px;
}
.statusWraper .popupStyle{
  width: 100%;
}
.settingsButton{
    display:block;
    width: 15px;
    height: 15px;
    float: left;
    margin: 8px 5px 0px 0px;
}
.controlButton{
    display:block;
    width: 16px;
    height: 16px;
    float: left;
    margin: 8px 5px 0px 0px;
}
.statusButton{
    display:block;
    width: 16px;
    height: 16px;
    float: left;
    margin: 8px 5px 0px 0px;
}
.iconButton{
    border: 0px;
    background:#f5f5f5 !important;
    font-size: 14px;
    color: #666;
    padding: 0px 0px 0px 10px;
}
.settingsIcon{
  background: url('./images/setting.svg') no-repeat;
}
.controlIcon{
  background: url('./images/control_panel.svg') no-repeat;
}
.menuItemAgent {
  margin-left: .5em;
  max-width: 140px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  float: left;
}
.menuItemStatus {
  max-width: 80px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  float: left;
}
.menuItem:hover .iconButton{
    color: #329fd9;
}
.menuItem:hover .controlIcon{
    background: url('./images/control_panelblue.svg') no-repeat;
}
.menuItem:hover .settingsIcon{
    background: url('./images/settingblue.svg') no-repeat;
}
.agentStatusItem{
    min-width: 120px;
}
.statusIcon{
    display: block;
    width:16px;
    height:16px;
    margin: 3px 0px 0px 0px;
    float: left;
}
.logoStyle{
    display: block;
    width: 200px;
    height: 28px;
    background-size: contain;
    float: left;
    cursor: pointer;
    margin: 14px 0px 0px 20px;
}
.menuContainer{
    border: 1px solid #ddd;
    background: #fff;
    border-radius: 3px;
    box-shadow: 0px 0px 5px rgba(0,0,0,0.15);
    -moz-shadow: 0px 0px 5px rgba(0,0,0,0.15);
    -webkit-box-shadow: 0px 0px 5px rgba(0,0,0,0.15);
    visibility: hidden;
}
.menuItem.mouseover .menuContainer {
    visibility: visible;
}
.trialInfoWraper {
    position: absolute;
    top: 0px;
    left:50%;
    width: 260px;
    height: 25px;
    margin-left: -130px;
    /*padding: 0px 30px;*/
    background: #fddd9f;
    line-height: 25px;
    border-radius: 0px 0px 4px 4px;
    font-size: 14px;
    text-align: center;
    z-index: 100;
}
.trialInfoWraper a{
    padding: 0px 0px 0px 20px;
}
.trialInfoWraper a:hover{
    text-decoration: underline;
}
.trialInfoWraper .purchase{
    margin-left: 10px;
    color: #319fd9;
    cursor: pointer;
}
.trialInfoWraper .label{
    color: #666;
}
.errorMessage {
  position: fixed;
  top: 0px;
  width: 100%;
  text-align: center;
  background: #ffe5dd;
  height: 54px;
  line-height: 54px;
  font-size: 14px;
  color: #eb4f43;
  z-index: 9999;
}
.dismiss{
  color: #329fd9;
  font-size: 14px;
  text-decoration: underline;
  cursor: pointer;
}
.errorMessage .label {
    margin: 0px 15px 0px 10px;
}
.closeIcon {
    display: inline-block;
    height:16px;
    padding: 0px;
    margin: 0px;
    width: 16px;
    border-radius: 50%;
    background: #f42a23;
    line-height: 16px;
    font-size: 16px;
    color: #fff;
    transform: rotate(45deg);
}



