.chatHeader{
	margin: 15px 10px 10px 0;
}
.chatHeader .visitorDetails{
	float: left;
}
.chatHeader .avatar{
	float: left;
	width: 50px;
	height: 50px;
}
.chatHeader .avatar img{
  display: block;
  width:100%;
  height: 100%;
	border: 1px solid #ddd;
}

.chatHeader .details{
	float: left;
	margin-left: 10px;
	margin-top: 2px;
  height: 41px;


}
.chatHeader .details .name{
	margin-bottom: 3px;
	font-size: 16px;
	color: #000;
	font-family: 'SourceSans Pro Bold', Arial;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
	max-width: 480px;
  /*max-width: 350px;*/
}

.chatHeader .details .others {
	height: 18px;
}
.chatHeader .details .others span{
	float: left;
	margin-right: 10px;
}

.chatHeader .action{
	float: right;
	margin-top: 18px;
}
.chatHeader .action .btnAction{
  margin-right: 10px;
  padding: 4px 15px;
  font-size: 14px;
}
.chatHeader .action > div:last-child .btnAction, .chatHeader .action .btnAaction:last-child{
	margin-right: 0
}
.chatHeader .tooltip{
  padding: 4px 8px;
  font-size: 12px;
}
.chatHeader .action > div{
  display: inline-block
}
.marginRight10 {
  margin-right: 10px;
}



