.Container {
    position: relative;
    width: 100%;
    background-color: #fedb9e;
    border: 1px solid #ffc253;
    border-radius: 2px;
    padding: 10px;
    margin-bottom: 6px;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    padding-bottom: 6px;
}
.Container:before {
    left: 10px;
    bottom: -13px;
    transform: translateX(-50%);
    border-color: #fedb95 transparent transparent transparent;
    border-width: 8px;
    z-index: 1;
}
.Container:after {
    left: 10px;
    bottom: -13px;
    transform: translateX(-50%);
    border-color: #ffc253 transparent transparent transparent;
    border-width: 6px;
    z-index: 0;
}
.Container:before , .Container:after {
    content: '';
    display: block;
    position: absolute;
    width: 0;
    height: 0;
    border-style: solid;
}
.text {
    font-size: 14px;
    color: #505050;
    line-height: 17px;
    padding-right: 5px;
    word-break: break-word;
    width: calc(100% - 10px);
}
.action {
    font-size: 10px;
    cursor: pointer;
}
.action i{
  font-size: 10px;
}



