.promptWindow{
  position: absolute;
  z-index: 999;
  bottom: 45px;
  left: -20px;
  min-width: 280px;
  background-color: #fff;
  padding: 30px;
  border-radius: 4px;
  outline: none;
  border:1px solid #ddd;
  box-shadow: 0 0 10px rgba(0,0,0,.15);
}
.promptWindow h3{
  margin: 0 0 15px;
  font-size: 18px;
  line-height: 1;
  text-align: center;
  font-family: 'SourceSans Pro Bold',Arial;
}
.promptWindow:before{
  content: '';
  position: absolute;
  left: 31px;
  bottom: -9px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 8px solid #ddd;
}
.promptWindow:after{
  content: '';
  position: absolute;
  left: 31px;
  bottom: -8px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 8px solid #fff;
}
.promptWindow .close{
  position: absolute;
  top: 10px;
  right: 10px;
  font-weight: 700;
  line-height: 1;
  color: #bbb;
  text-shadow: 0 1px 0 #fff;
}
.promptWindow .close:focus, .promptWindow .close:hover {
  color: #969696;
  text-decoration: none;
  cursor: pointer;
  outline: none;
}
.promptWindow button.close{
  -webkit-appearance: none;
  padding: 0;
  cursor: pointer;
  background: 0 0;
  border: 0;
}

.promptWindow .action{
  margin-top:25px;
  text-align: center;
}
.promptWindow .action button{
  margin-right: 15px;
  width: 80px;
  padding-left: 15px;
  padding-right: 15px;
}
.promptWindow .action button:last-child{
  margin-right: 0;
}
.dropdown{
  z-index: 1000;
}


