.headerContainer {
  height: 35px;
  user-select: none;
  white-space: nowrap;
  min-width: 100%;
  display: flex;
}

div.top {
  height: 55px;
}

div.topInfo {
  margin-left: 20px;
  padding-top: 18px;
  display: inline-block;
}

div.topInfoLine {
  display: inline-block;
  margin-right: 20px;
}

div.topInfoLine span {
  font-weight: 800;
  color: #319ed7;
  padding-right: 5px;
}

ul.tab {
  flex-grow: 0;
  user-select: none;
}

ul.tab li {
  display: inline-block;
  background-color: #fff;
  border: 1px solid #cccccc;
  border-left: none;
  color: #666;
  font-size: 13px;
  padding-left: 20px;
  padding-right: 20px;
  cursor: pointer;
}

ul.tab li.selected {
  background-color: #aaa;
  color: #fff;
  cursor: default;
}

ul.tab li:hover{
  color: #000000;
  border-color: #b4b4b4;
}

.headerContainer .headerCell:first-child {
  /*border-left: 1px solid #cccccc;*/
  /*border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;*/
}

.headerContainer .headerCell:last-child {
  flex-grow: 1;
}

.headerCell {
  display: inline-block;
  height: 35px;
  border-width: 1px;
  border-color: #ddd;
  border-right-style: solid;
  border-top-style: solid;
  border-bottom-style: solid;
  cursor: pointer;
  line-height: 35px;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-shrink: 0;
  user-select: none !important;
  /**
   * Fix issue 19076.
   * Default box-sizing in our project is `border-box`. However, in IE, using `border-box`, even
   * though `height` is set to `30px`, `getComputedStyle` will return `height` as `28px`, as
   * border-width is excluded.
   * This will cause issue 19076, as react-resizable-box use computed height to re-assign the value.
   * Thus, force to use `content-box` here.
   */
  box-sizing: content-box;
}

.dragging {
  border: 1px solid #ddd;
}

.headerCellContent {
  padding-left: 10px;
  padding-right: 10px;
  color: #222;
  display: flex;
}

.headerCell span {
  /*flex-grow: 1;*/
  flex-shrink: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  display: inline-block;
}

.headerCellContent i {
  transition: none;
  margin-left: 5px;
  flex-grow: 0;
  flex-shrink: 0;
  text-align: right;
  line-height: 35px;
}

.headerStatusCell {
  height: 35px;
}

.headerStatusCell i {
  transition: none;
  line-height: 30px;
  padding-left: 10px;
  position: relative;
}

.visitorInfoLine {
  line-height: 35px;
  height: 35px; /* fix issue 19437 */
  vertical-align: middle;
  display: flex;
}

.visitorInfoLine span {
  flex-shrink: 0;
  flex-grow: 0;
}

.visitorInfoLine a {
  flex-shrink: 0;
  flex-grow: 0;
  display: inline-block;
}

.visitorInfoLine span.visitorInfoName {
  color: #666;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  flex-shrink: 1;
  cursor: default;
  display: inline-block;
}
.visitorCountry {
  padding-right: 15px;
}

.visitorInfoLine > i {
  margin-top: 10px;
  flex-shrink: 0;
}

.visitorInfoLine a {
  color: #319ed7;
  padding-left: 15px;
  cursor: pointer;
}

.visitorInfoExtraLine {
  height: 20px;
  margin-top: 5px;
}

.visitorInfoExtraLine a {
  color: #646464;
  padding-left: 14px;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
}
.visitorInfoExtraLine > a:first-child {
  padding-left: 0;
}

/*.visitorInfoExtraLine button {
  padding-top: 0px;
  padding-bottom: 0px;
  height: 22px;
}*/

.columnRow {
  display: flex;
}

.columnRow.odd:hover, .columnRow.even:hover {
  background-color: #e6e6e6;
}

.columnRow.odd {
  background-color: #fff;
}

.columnRow.even {
  background-color: #f5f5f5;
}

.columnRow > div {
  padding-left: 10px;
  padding-right: 10px;
  user-select: none;
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  border-right: 1px solid transparent;
  flex-shrink: 0;
}

.columnRow > div:last-child {
  flex-grow: 1;
}

.columnCellHeight {
  height: 35px;
  line-height: 35px;
}

.columnRow > div > span, .columnRow > div > a {
  color: #666;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-grow: 0;
  flex-shrink: 1;
}
.columnRow > div > span {
  cursor: default;
}

.columnRow > div > span, .columnRow > div > a, .columnRow > div > i {
  display: inline-block;
  /**
   * Use padding instead of margin, so that tooltip can correctly calculate height of element and
   * center the tooltip correctly.
   */
  line-height: 35px;
}
.columnRow > div > i {
  padding-top: 2px;
}

.columnRow > div > a, .visitorInfoLine a, .visitorInfoExtraLine a {
  text-decoration: underline;
}

.customerSegmentationCell {
  line-height: 35px;
}

.customerSegmentationCell span {
  width: 10px;
  height: 18px;
  vertical-align: middle;
  display: inline-block;
  border: 1px solid rgba(0,0,0,.2);
}

.tooltip {
  font-size: 12px !important;
  padding: 5px 10px !important;
  text-indent: 0px;
  max-width: 200px;
  word-wrap: break-word;
}

.segmentBlock {
  display: inline-block;
  width: 5px;
  height: 5px;
  margin-right: 5px;
}
