.nofilterDiv {
  width: 640px;
  height: 250px;
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  user-select: none;
  cursor: default;
}
.nofilterDiv h1 {
  margin: 0;
  font-size: 22px;
  line-height: 32px;
}
.nofilterImgWrapper{
  margin: 5px auto;
  width: 165px;
  height: 165px;
  background-repeat: no-repeat;
  background-position-y: center;
  background-size: contain;
  background-image: url('./no-filters.png');
}
.popuicon {
  float: left;
