.container {
  min-width: 265px;
  width: 100%;
  height: 100%;
  max-height: 100%;
  border: 1px solid #ddd;
  position: absolute;
  user-select: none;
  background-color: #fff;
}
.content{
  position: absolute;
  top: 40px;
  bottom: 0;
  width: 100%;
}
.header {
  display: flex;
}

.header > div {
  border-right: 1px solid #ddd;
  overflow: hidden;
  padding-left: 8px;
  padding-right: 8px;
}

.header > div:last-child {
  border-right-width: 0px;
}
.content {
  position: absolute;
  top: 40px;
  bottom: 0;
  overflow: auto;
}
.icon {
  display: inline-block;
  padding: 10px 12.5px;
  height: 40px;
}

.selected {
  flex: 1;
  display: flex;
  align-items: center;
  cursor: default;
}

.selected .icon {
  padding: 10px;
}

.selected span {
  font-size: 14px;
  display: inline-block;
  color: #333;
  font-family: "SourceSans Pro Bold";
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding-left: 0.5em;
  padding-right: 0.5em;
  width: calc(100% - 20px);
}

.unselected {
  flex: none;
  border-bottom: 1px solid #ddd;
  cursor: pointer;
}

.tooltip{
  padding: 4px 6px !important;
  font-size: 12px !important;
  font-style: normal;
  max-width: 250px;
  word-wrap: normal;
  word-break: break-all;
  text-indent: 0px;
  text-transform: capitalize;
  font-family: "SourceSans Pro", Arial;
}



