.history {
    width: 100%;
    height: 100%;
    padding-top: 15px;
    padding-bottom: 15px;
    word-break: break-word;
    text-overflow: ellipsis;
    overflow: auto;
}
.infoline{
  padding-bottom: 15px;
  padding-left: 15px;
  color:#bbb;
  font-size: 14px;
  line-height: 1;
}
.historyType{
  padding-bottom: 5px;
}
.viewTypeSwitch{
  margin-top:10px;
}
.historyItem{
  line-height: 18px;
  padding: 3px 15px 8px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.detailLoading {
  padding: 0 0 5px 15px;
  color:#666666;
  font-size: 14px;
}
.listItem{
  color: #666;
  /*margin: 5px 10px;*/
  cursor: pointer;
}

.detail{
 border: 1px solid #ccc;
 background: #e6e6e6;
 margin-top: 8px;
 padding: 15px 15px 20px 15px;
 width: 100%;
 /*box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.15);*/
}
.detail:after,
.detail:before {
    content: '';
    position: absolute;
    border-style: solid;
    left: 25px;
}
.detail:after {
    top: -16px;
    transform: translateX(-50%);
    border-color: transparent transparent #ccc  transparent;
    border-width: 8px;
    z-index: 999;
    top: -15px;
    box-shadow: 0 5px #e6e6e6;
}
.detail:before {
    top: -12px;
    transform: translateX(-50%);
    border-color: transparent transparent #e6e6e6 transparent;
    border-width: 6px;
    z-index: 1000;
    top: -11px;
}

.detailError{
  color:#d34946;
  font-size: 14px;
  line-height: 18px;
  margin: 4px 15px;
}

.detailErrorRrefresh{
  margin-left:10px;
  color:#329fd9;
}

.title{
    color: #000;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 1;
}
.information{
  color:#666;
  font-size: 15px;
  line-height: 18px;
  white-space: pre-line;
  /*-webkit-user-select: text;*/
  user-select: text;
}
.information > div {
  word-wrap: break-word;
}
.information a:hover {
  text-decoration: underline;
}
.splitLine{
  margin: 15px 0 20px 0;
  border-bottom: 1px dashed #ccc;
}
.detailLink{
  float: right;
  font-size: 14px;
  line-height: 1;
}
.Loading{
   padding-left: 10px;
}
.moreChat{
  /*position: absolute;*/
  bottom: 0px;
  margin-top: 25px;
  text-align: center;
  font-size: 12px;
  line-height: 1;
  cursor: pointer;
  width: 100%;
}
.moreChat a{
  color:#329fd9;
}
.moreText{
  font-size: 14px;
  display: inline-block;
  padding-left: 6px;
}
.moreChat a i {
  position: relative;
  top: 2px;
}
.visitorMessage{
  font-family: "SourceSans Pro Bold";
  color: #329FD9;
}
.agentMessage{
  font-family: "SourceSans Pro Bold";
  color: #444444;
}

.tooltip{
  padding: 4px 20px;
  font-size: 12px;
  font-style: normal;
  max-width: 300px;
  word-wrap: normal;
  word-break: break-all;
}


