.container {
  display: flex;
  justify-content:space-between;
  align-items:center;
}

.optionsWrapper {
  height: 150px;
  overflow: auto;
}

.option {
  padding: 5px;
  line-height: 1;
  color: #666;
}
.option:hover {
  background: #dfdfdf;
  cursor: default;
}
.selected {
  background: #cce7f5;
}
.operations {
  width: 20px;
}
.icons {
  display: block;
  width: 20px;
  height: 20px;
}
.operation {
  margin: 5px 0;
  color: #bbbbbb;
}
.operation:hover {
  color: #828282;
  cursor: pointer;
}
.rightMargin {
  margin-right: 10px;
}
.leftMargin {
  margin-left: 10px;
}
.header{
  margin: 0;
  padding: 0 5px;
  font-style: italic;
  margin-right: 10px;
}
.wrapper {
  width: 45%;
  border: 1px solid #ccc;
  border-radius: 2px;
  margin-top:3px;
}


