.container {
  position: absolute;
  width: 100%;
  height: 175px;
  bottom: 0;
  min-height: 40px;
  border: 1px solid #ddd;
}

.container.focused {
  border: 1px solid #329FD9;
}

.container.disabled {
  cursor: default;
  background: #f0f0f0;
}

.container :global(.public-DraftEditor-content) {
  height: 140px;
  padding: 10px;
  overflow-y: auto;
}

.container.dragging :global(.public-DraftEditor-content) {
  background: url('./images/dragHere.png') no-repeat center center;
  background-color: #fddfa3;
}

.container.dragging :global(.public-DraftEditor-content) > * {
  display: none;
}

.dnd {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: url('./images/dragHere.png') no-repeat center center;
  background-color: #fddfa3;
}

.selection {
  background-color: #fff;
  position: fixed;
  box-shadow: 1px 1px 5px 1px rgba(0,0,0,.18);
  width: 420px;
  cursor: default;
  z-index: 2;
}

.selected, .unselected {
  padding-right: 15px;
  padding-left: 15px;
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.selected {
  background-color: #d6ecf7;
}

.unselected {
  background-color: #fff;
}

.unselected:hover {
  background-color: #dfdfdf;
}

.selection.ie .unselected:hover {
  background-color: #fff;
}

.selection .key {
  padding-right: 15px;
}

.selection .value {
  color: #888;
}

.buttonGroup {
  position: absolute;
  bottom: 5px;
  right: 5px;
  height: 25px;
  font-size: 14px;
  padding: 0;
  display: flex;
  line-height: 1;
  z-index: 2;
}

.buttonGroup button {
  height: 25px;
  display: flex;
  justify-content: center;
  align-content: center;
}

.buttonGroup button:first-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  padding-right: 4px;
  line-height: 1;
}
.buttonGroup button:last-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.buttonGroup:hover button{
  background: #0d92d8;
  border-color: #0d92d8;
}

.buttonGroup .arrowDownIcon{
  padding: 0;
  width: 20px;
  line-height: 25px;
}

.buttonGroup .arrowDownIcon i {
  line-height: 23px;
}

.sendType {
  position: absolute;
  right: 5px;
  bottom: 30px;
  width: 200px;
  background-color: #fff;
  border: 1px solid #ddd;
  z-index: 2;
}

.sendType .unselectedLine {
  padding-left: 28px;
  line-height: 25px;
  cursor: pointer;
}

.sendType .unselectedLine:hover {
  background-color: #ededed;
}

.sendType .unselectedLine i {
  display: none;
}

.sendType .selectedLine {
  padding-left: 10px;
  line-height: 25px;
  background-color: #ededed;
  cursor: default;
}

.sendType .selectedLine i {
  margin-right: 8px;
}

.chatContentsmall{
  font-size: 13px;
}
.chatContentmiddle{
  font-size: 15px;
}
.chatContentlarge{
  font-size: 17px;
}
.directionRTL :global(.public-DraftStyleDefault-ltr){
  direction: rtl;
  text-align: right;
}



