.chatContentContainer{
  position: absolute;
  bottom: 215px;
  top: 0;
  width: 100%;
}
.chatContent{
  position: absolute;
  bottom: 0;
  top: 0;
  width: 100%;
	border:1px solid #ddd;
	padding: 18px 15px;
	overflow: auto;
}
.chatContent.directionRTL{
  direction: rtl;
}
.chatContent .systemMeg{
	font-size: 13px;
	color:#888;
	margin-bottom: 6px;
}

.chatContent .chatMeg{
  display: inline-block;
	white-space: pre-wrap;
}
.chatContent .chatMeg i, .chatContent .chatMeg .translationMsg{
  color: #8a8a8a;
}
.chatContent .chatMeg a:link, .chatContent .chatMeg a:visited{
  text-decoration: underline;
}
.chatContent .chatMeg .attachment{
  display: inline-block;
  /** @todo can use font instead */
  background: url('./images/sendfile.png') no-repeat left center;
  padding-left: 22px;
  text-decoration: underline;
}
.chatContent .chatContentInfo{
  margin-bottom: 13px;
}
.chatContent .chatContentInfo .name{
  font-size: 15px;
  font-family: SourceSans Pro Bold;
}
.chatContent .chatContentInfo .time{
  font-size: 14px;
}
.chatContent .chatContentInfo .chatMeg{
  position: relative;
  margin-top: 7px;
  color:#000;
  font-size: 13px;
  padding: 12px 15px;
  border-radius: 2px;
}
.chatContent .chatContentInfo .chatMeg:before{
  content: "";
	position: absolute;
  left: 16px;
  top: -5px;
	width: 0;
	height: 0;
	border-left: 7px solid transparent;
	border-right: 7px solid transparent;
}
.chatContent.directionRTL .chatContentInfo .chatMeg:before{
  right: 22px;
  left: 0;
}
.chatContent .chatContentInfo .chatMeg p{
	margin-top: 0;
	margin-bottom: 5px;
  word-break: break-word;
}
.chatContent .chatContentInfo .chatMeg p:last-child{
	margin-bottom: 0
}

.chatContent .operator .name{
	color:#666;
}
.chatContent .operator .time{
	color: #bbb;
}
.chatContent .operator .chatMeg{
	background-color: #f2f2f2;
}
.chatContent .operator .chatMeg:before{
	border-bottom: 5px solid #f2f2f2;
}


.chatContent .visitor .name{
	color:#329fd9;
}
.chatContent .visitor .time{
	color: #bbb;
}
.chatContent .visitor .chatMeg, .chatContent .visitor .secureForm{
	background-color: #cce7f5;
}
.chatContent .visitor .chatMeg:before{
	border-bottom: 5px solid #cce7f5;
}
.chatContent .visitor .secureForm{
  display: inline-block;
  padding: 5px 10px;
}
.chatContent .visitor .secureForm i{
  display: block;
}

.chatContent .typingIndicator{
  display: inline-block;
	color: #888;
	font-style: italic;
	font-size: 14px;
  word-break: break-word;
}
.chatContent .typingIndicator span {
  font-family: "SourceSans Pro Bold";
}

.chatContent.chatContentsmall .systemMeg{
	font-size: 13px;
}
.chatContent.chatContentmiddle .systemMeg{
	font-size: 15px;
}
.chatContent.chatContentlarge .systemMeg{
	font-size: 17px;
}
.chatContent.chatContentsmall .name{
  font-size: 13px;
}
.chatContent.chatContentmiddle .name{
  font-size: 15px;
}
.chatContent.chatContentlarge .name{
  font-size: 17px;
}
.chatContent.chatContentsmall .time{
  font-size: 12px;
}
.chatContent.chatContentmiddle .time{
  font-size: 14px;
}
.chatContent.chatContentlarge .time{
  font-size: 16px;
}
.chatContent.chatContentsmall .chatMeg, .chatContent.chatContentsmall .typingIndicator, .chatContent.chatContentsmall .secureForm{
	font-size: 13px;
}
.chatContent.chatContentmiddle .chatMeg, .chatContent.chatContentmiddle .typingIndicator, .chatContent.chatContentmiddle .secureForm{
	font-size: 15px;
}
.chatContent.chatContentlarge .chatMeg, .chatContent.chatContentlarge .typingIndicator, .chatContent.chatContentlarge .secureForm{
	font-size: 17px;
}

.chatContentContainer .notify{
  position: absolute;
  width: 100%;
  width: calc(100% - 2px);
  left: 1px;
  bottom: 1px;
  background: #fedb9e;
  padding: 6px 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 60px;
}

.chatContentContainer .notify:hover{
  cursor: pointer;
  background: #ffd165;
}
.chatContentContainer .notifyG2M:hover{
  cursor: default;
  background: #fedb9e;
}
.chatContentContainer .notifyName{
  font-weight: 700;
}
.error{
  color: #ec4145;
  padding-left: 12px;
  padding-right: 12px;
  background-color: #ffe6dd;
  bottom: 1px;
  left: 1px;
  position: absolute;
  width: calc(100% - 2px);
  height: 30px;
  line-height: 30px;
}
.error .errorContent{
  display: inline-block;
  max-width: calc(100% - 63px);
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.error button{
  position: relative;
  top: -10px;
  margin-left: 15px;
  text-decoration: underline;
}
.chatContentContainer .notify .action{
  margin-right: 30px;
}
.chatContentContainer .notify .action a, .chatContentContainer .notify .action button{
  margin-left: 20px;
  text-decoration: underline;
}
.chatContentContainer .notify .action button, .error button{
  color: #329fd9!important;
  padding: 0;
}



